body {
  margin: 0;
  font-family: Libre Baskerville, cursive;
}

.wrapper {
  max-width: 450px;
  z-index: 3;
  background-color: rgba(255, 255, 255, .5);
  border-radius: 50px;
  margin: 100px auto auto;
  padding: 50px;
  position: relative;
  box-shadow: 13px 9px 21px 8px rgba(0, 0, 0, .75);
}

.weather__city {
  text-shadow: 3px 7px 9px #000;
  font-family: Rubik Glitch, cursive;
  font-size: 50px;
}

.weather__status {
  margin-bottom: 5px;
  font-size: 25px;
}

.weather__temp, .weather__feels-likes {
  font-size: 25px;
}

.weather__main {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.span {
  margin-left: 15px;
}

.search-form {
  z-index: 3;
  justify-content: center;
  align-items: baseline;
  display: flex;
  position: relative;
}

.btn {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  padding: 12px;
  box-shadow: 17px 17px 21px 3px rgba(0, 0, 0, .83);
}

input {
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 20px;
  margin-top: 10px;
  margin-right: 10px;
  padding: 10px;
  box-shadow: 17px 17px 21px 3px rgba(0, 0, 0, .83);
}

::-ms-input-placeholder {
  color: #000;
}

::placeholder {
  color: #000;
}

.video {
  z-index: 1;
  position: relative;
}

.video_media {
  width: 100%;
  height: auto;
  position: absolute;
}

.intro {
  max-height: 100vh;
}

.intro:after {
  content: "";
  z-index: 2;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

